import { useEffect, useState } from "react";
import { Box, Button, TextField, useMediaQuery, Typography, useTheme } from "@mui/material";
import EditOutlinedIcon from "@mui/icons-material/EditOutlined";
import { Formik } from "formik";
import * as yup from "yup";
import { useNavigate } from "react-router-dom";
import { useDispatch } from "react-redux";
import { setLogin } from "state";
import Dropzone from "react-dropzone";
import FlexBetween from "components/FlexBetween";
import FacebookIcon from "@mui/icons-material/Facebook";
import GitHubIcon from "@mui/icons-material/GitHub";
import GoogleIcon from "@mui/icons-material/Google";

const registerSchema = yup.object().shape({
  firstName: yup.string().required("required"),
  lastName: yup.string().required("required"),
  email: yup.string().email("invalid email").required("required"),
  password: yup.string().required("required"),
  location: yup.string().required("required"),
  occupation: yup.string().required("required"),
  picture: yup.string().required("required"),
});

const loginSchema = yup.object().shape({
  email: yup.string().email("invalid email").required("required"),
  password: yup.string().required("required"),
});

const initialValuesRegister = {
  firstName: "",
  lastName: "",
  email: "",
  password: "",
  location: "",
  occupation: "",
  picture: "",
};

const initialValuesLogin = {
  email: "",
  password: "",
};

const SocialLoginButton = ({ icon, text, onClick, color }) => {
  return (
      <Button
          variant="outlined"
          startIcon={icon}
          onClick={onClick}
          sx={{
            textTransform: "none",
            borderColor: color,
            color: color,
            flex: 1,
            borderRadius: 1,
            "&:hover": {
              borderColor: color,
              backgroundColor: color,
              color: "#ffffff",
            },
          }}
      >
        {text}
      </Button>
  );
};

const Form = () => {
  const [pageType, setPageType] = useState("login");
  const { palette } = useTheme();
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const isNonMobile = useMediaQuery("(min-width:600px)");
  const isLogin = pageType === "login";
  const isRegister = pageType === "register";

  const register = async (values, onSubmitProps) => {
    const formData = new FormData();
    for (let value in values) {
      formData.append(value, values[value]);
    }
    formData.append("picturePath", values.picture.name);

    try {
      const savedUserResponse = await fetch("http://localhost:3001/auth/register", {
        method: "POST",
        body: formData,
      });
      const savedUser = await savedUserResponse.json();
      onSubmitProps.resetForm();

      if (savedUser) {
        setPageType("login");
      }
    } catch (error) {
      console.error("Error during registration:", error);
    }
  };

  const login = async (values, onSubmitProps) => {
    try {
      const loggedInResponse = await fetch("http://localhost:3001/auth/login", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(values),
      });
      const loggedIn = await loggedInResponse.json();
      console.log(loggedIn.user)
      onSubmitProps.resetForm();
      if (loggedIn) {
        dispatch(
            setLogin({
              user: loggedIn.user,
              token: loggedIn.token,
            })
        );
        navigate("/home");
      }
    } catch (error) {
      console.error("Error during login:", error);
    }
  };

  const handleFormSubmit = async (values, onSubmitProps) => {
    if (isLogin) await login(values, onSubmitProps);
    if (isRegister) await register(values, onSubmitProps);
  };

  const handleFacebookLogin = () => {
    window.location.href = `http://localhost:3001/auth/facebook?appId=YOUR_FACEBOOK_APP_ID&redirectUri=${window.location.origin}/auth/facebook/callback`;
  };

  const handleGitHubLogin = () => {
    window.location.href = `http://localhost:3001/auth/github?clientId=7b9ae8b3e8ce923fe4e6&redirectUri=${window.location.origin}/auth/github/callback`;
  };

  const handleGoogleLogin = () => {
    window.location.href = `http://localhost:3001/auth/google?clientId=YOUR_GOOGLE_CLIENT_ID&redirectUri=${window.location.origin}/auth/google/callback`;
  };

  return (
      <Formik
          onSubmit={handleFormSubmit}
          initialValues={isLogin ? initialValuesLogin : initialValuesRegister}
          validationSchema={isLogin ? loginSchema : registerSchema}
      >
        {({
            values,
            errors,
            touched,
            handleBlur,
            handleChange,
            handleSubmit,
            setFieldValue,
            resetForm,
          }) => (
            <form onSubmit={handleSubmit}>
              <Box
                  display="grid"
                  gap="30px"
                  gridTemplateColumns="repeat(4, minmax(0, 1fr))"
                  sx={{
                    "& > div": { gridColumn: isNonMobile ? undefined : "span 4" },
                  }}
              >
                <Box
                    gridColumn="span 1"
                    display="flex"
                    alignItems="center"
                    justifyContent="center"
                    mt={2}
                >
                  <SocialLoginButton
                      icon={<GitHubIcon />}
                      text="Login with GitHub"
                      onClick={handleGitHubLogin}
                      color="#000000"
                  />
                </Box>

              {isRegister && (
                    <>
                      <TextField
                          label="First Name"
                          onBlur={handleBlur}
                          onChange={handleChange}
                          value={values.firstName}
                          name="firstName"
                          error={Boolean(touched.firstName) && Boolean(errors.firstName)}
                          helperText={touched.firstName && errors.firstName}
                          sx={{ gridColumn: "span 2" }}
                      />
                      <TextField
                          label="Last Name"
                          onBlur={handleBlur}
                          onChange={handleChange}
                          value={values.lastName}
                          name="lastName"
                          error={Boolean(touched.lastName) && Boolean(errors.lastName)}
                          helperText={touched.lastName && errors.lastName}
                          sx={{ gridColumn: "span 2" }}
                      />
                      <TextField
                          label="Location"
                          onBlur={handleBlur}
                          onChange={handleChange}
                          value={values.location}
                          name="location"
                          error={Boolean(touched.location) && Boolean(errors.location)}
                          helperText={touched.location && errors.location}
                          sx={{ gridColumn: "span 4" }}
                      />
                      <TextField
                          label="Occupation"
                          onBlur={handleBlur}
                          onChange={handleChange}
                          value={values.occupation}
                          name="occupation"
                          error={Boolean(touched.occupation) && Boolean(errors.occupation)}
                          helperText={touched.occupation && errors.occupation}
                          sx={{ gridColumn: "span 4" }}
                      />
                      <Box
                          gridColumn="span 4"
                          border={`1px solid ${palette.neutral.medium}`}
                          borderRadius="5px"
                          p="1rem"
                      >
                        <Dropzone
                            acceptedFiles=".jpg,.jpeg,.png"
                            multiple={false}
                            onDrop={(acceptedFiles) => setFieldValue("picture", acceptedFiles[0])}
                        >
                          {({ getRootProps, getInputProps }) => (
                              <Box
                                  {...getRootProps()}
                                  border={`2px dashed ${palette.primary.main}`}
                                  p="1rem"
                                  sx={{ "&:hover": { cursor: "pointer" } }}
                              >
                                <input {...getInputProps()} />
                                {!values.picture ? (
                                    <p>Add Picture Here</p>
                                ) : (
                                    <FlexBetween>
                                      <Typography>{values.picture.name}</Typography>
                                      <EditOutlinedIcon />
                                    </FlexBetween>
                                )}
                              </Box>
                          )}
                        </Dropzone>
                      </Box>
                    </>
                )}

                <TextField
                    label="Email"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    value={values.email}
                    name="email"
                    error={Boolean(touched.email) && Boolean(errors.email)}
                    helperText={touched.email && errors.email}
                    sx={{ gridColumn: "span 4" }}
                />
                <TextField
                    label="Password"
                    type="password"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    value={values.password}
                    name="password"
                    error={Boolean(touched.password) && Boolean(errors.password)}
                    helperText={touched.password && errors.password}
                    sx={{ gridColumn: "span 4" }}
                />
              </Box>

              {/* BUTTONS */}
              <Box>
                <Button
                    fullWidth
                    type="submit"
                    sx={{
                      m: "2rem 0",
                      p: "1rem",
                      backgroundColor: palette.primary.main,
                      color: palette.background.alt,
                      "&:hover": { color: palette.primary.main },
                    }}
                >
                  {isLogin ? "LOGIN" : "REGISTER"}
                </Button>
                <Typography
                    onClick={() => {
                      setPageType(isLogin ? "register" : "login");
                      resetForm();
                    }}
                    sx={{
                      textDecoration: "underline",
                      color: palette.primary.main,
                      "&:hover": {
                        cursor: "pointer",
                        color: palette.primary.light,
                      },
                    }}
                >
                  {isLogin
                      ? "Don't have an account? Sign Up here."
                      : "Already have an account? Login here."}
                </Typography>
              </Box>
            </form>
        )}
      </Formik>
  );
};

export default Form;
